p {font-family:"Protest Revolution";}
.cat-info {
    width: 100%;
    height: 100%;
   
}

.cat-info img {
    width: 360px;
    height: 400px;
    margin-top: 10px;
}



.loader {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: block;
    margin: 15px auto;
    position: relative;
    box-sizing: border-box;
    animation: zeroRotation 1s linear infinite alternate;
}

.loader::after,
.loader::before {
    content: '';
    box-sizing: border-box;
    position: absolute;
    left: 0;
    top: 0;
    background: #FF3D00;
    width: 16px;
    height: 16px;
    transform: translate(-50%, 50%);
    border-radius: 50%;
}

.loader::before {
    left: auto;
    right: 0;
    transform: translate(50%, 100%);
}

@keyframes zeroRotation {
    0% {
        transform: scale(1) rotate(0deg);
    }

    100% {
        transform: scale(0) rotate(360deg);
    }
}