p {
  font-family: Protest Revolution;
}

.cat-info {
  width: 100%;
  height: 100%;
}

.cat-info img {
  width: 360px;
  height: 400px;
  margin-top: 10px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  animation: zeroRotation 1s linear infinite alternate;
  display: block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  background: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, 50%);
}

.loader:before {
  left: auto;
  right: 0;
  transform: translate(50%, 100%);
}

@keyframes zeroRotation {
  0% {
    transform: scale(1)rotate(0deg);
  }

  100% {
    transform: scale(0)rotate(360deg);
  }
}

